import { format } from "date-fns"
import ReactModal from "react-modal"
import React, { useEffect, useState } from "react"
import ModalLoading from "../../Components/Modals/ModalLoading"
import { IoNewspaperOutline } from "react-icons/io5"
import ModalHistory from "./ModalHistory"

export default function ModalEdit({ visible, onClose, userData, setUserData, setVisible }) {
    const [selectedDate, setSelectedDate] = useState(format(new Date(), "yyyy-MM-dd"))
    const [addValue, setAddValue] = useState("")
    const [modalLoading, setModalLoading] = useState(false)
    const [modalHistory, setModalHistory] = useState(false)
    const [history, setHistory] = useState(userData.pagoDias)

    const setCellValue = (cell, newValue) => {
        const sheetName = encodeURIComponent("ARRANJO DE ONIBUS");

        const url = `https://script.google.com/macros/s/AKfycbwz4hog7Ovzi2CEQFUMTzPxzHuSAqst2N_BvTNN9vxiS9P6MKhL4PpMB2mz6A2xlAws/exec?func=updateCell&sheetName=${sheetName}&cell=${cell}&value=${newValue}`
        fetch(url)
            .then(response => response.json())
            .then(data => {
                setModalLoading(false)
                setVisible(false)
            })
            .catch(error => {
                setModalLoading(false)
                console.error('Erro:', error)
            });
    }

    const getCellValue = (cell) => {
        return new Promise((resolve, reject) => {
            const sheetName = encodeURIComponent("ARRANJO DE ONIBUS");
            const url = `https://script.google.com/macros/s/AKfycbwz4hog7Ovzi2CEQFUMTzPxzHuSAqst2N_BvTNN9vxiS9P6MKhL4PpMB2mz6A2xlAws/exec?func=getCellValue&sheetName=${sheetName}&cell=${cell}`;

            fetch(url)
                .then(response => response.json())
                .then(data => resolve(data))
                .catch(error => reject("Erro: " + error));
        });
    };

    const confirmChanges = async () => {
        try {
            if (addValue != '') {
                const result = await getCellValue(`D${userData.index + 3}`)
                if (result.value != '') {
                    let arr = JSON.parse(result.value)
                    arr.push({ data: new Date(selectedDate), valor: addValue })
                    setCellValue(`D${userData.index + 3}`, JSON.stringify(arr))
                } else {
                    setCellValue(`D${userData.index + 3}`, JSON.stringify([{ data: new Date(selectedDate), valor: addValue }]))
                }

                setCellValue(`E${userData.index + 3}`, (parseFloat((userData.totalPago).replace(",", ".")) + parseFloat(addValue.replace(",", "."))).toString().replace(".", ","))
            }
        } catch (error) {
            alert(error)
        }
    }

    return (
        <>
            {modalLoading && <ModalLoading title="Salvando alterações na planilha..." visible={modalLoading} />}
            {modalHistory && <ModalHistory visible={modalHistory} setVisible={setModalHistory} history={history} />}

            <ReactModal
                isOpen={visible}
                style={{
                    overlay: {
                        backgroundColor: 'rgba(0,0,0,0.4)',
                        display: 'flex',
                        flex: 1,
                        justifyContent: 'center',
                        alignItems: 'center'
                    },
                    content: {
                        inset: 'auto',
                        margin: 'auto',
                        width: '70%'
                    }
                }}
                onRequestClose={() => onClose(false)}
            >
                <div>

                    <div style={{
                        borderWidth: 1,
                        borderColor: '#000',
                        borderStyle: 'solid',
                        padding: 10,
                        display: 'flex',
                        justifyContent: 'center',
                        flexDirection: 'column',
                        gap: 18,
                        position: 'relative'
                    }}>
                        <span style={{ position: 'absolute', top: 6, right: 10 }} className="button" onClick={() => setModalHistory(true)}>
                            <IoNewspaperOutline size={25} />
                        </span>

                        <span style={{}}>{userData.nome}</span>

                        <span style={{ display: 'flex', alignItems: 'center', gap: 10 }}>
                            <span>CPF/RG</span>
                            <input
                                value={userData.cpfOUrg}
                                onChange={event => setUserData(prev => ({ ...prev, cpfOUrg: event.target.value }))}
                                type="number"
                            />
                        </span>

                        <span style={{
                            display: 'flex',
                            flexDirection: 'column',
                            borderWidth: 1,
                            borderColor: '#000',
                            borderStyle: 'solid',
                            padding: 12,
                            gap: 10
                        }}>
                            <span>Adicionar novo pagamento</span>
                            <input
                                type="date"
                                value={selectedDate}
                                onChange={event => setSelectedDate(event.target.value)}
                            />
                            <div style={{ display: 'flex', alignItems: 'center', gap: 6 }}>
                                <span>R$</span>
                                <input
                                    value={addValue}
                                    onChange={event => setAddValue(event.target.value)}
                                    placeholder="Insira o valor"
                                    type="number"
                                />
                            </div>

                        </span>

                        <span>Total pago: R$ {userData.totalPago}</span>

                        <button onClick={() => {
                            confirmChanges()
                            setModalLoading(true)
                            setCellValue(`C${userData.index + 3}`, userData.cpfOUrg)

                        }}>Salvar Alterações</button>

                    </div>

                </div>

            </ReactModal>
        </>
    )
}