import React, { CSSProperties } from "react"
import ReactModal from "react-modal";
import ClipLoader from "react-spinners/ClipLoader";

export default function ModalLoading({ visible, onClose, title }) {
    return (
        <ReactModal
            isOpen={visible}
            //onRequestClose={onClose}
            style={{
                overlay: {
                    backgroundColor: "rgba(0,0,0,0.4)",
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flex: 1
                },
                content: {
                    width: '50%',
                    inset: 'auto',
                    margin: 'auto'
                }
            }}
        >
            <div style={{display: 'flex', alignItems: 'center', gap: 20}}>
                <ClipLoader
                    color={"#000"}
                    loading={true}
                    size={50}
                    aria-label="Loading Spinner"
                    data-testid="loader"
                    speedMultiplier={0.5}
                />
                <span>{title}</span>

            </div>

        </ ReactModal>
    )
}