import ReactModal from "react-modal"
import React, { useEffect, useState } from "react"
import { format } from "date-fns"

export default function ModalHistory({ visible, setVisible, history }) {
    return (
        <ReactModal
            isOpen={visible}
            style={{
                overlay: {
                    backgroundColor: 'rgba(0,0,0,0.4)',
                    display: 'flex',
                    flex: 1,
                    justifyContent: 'center',
                    alignItems: 'center'
                },
                content: {
                    inset: 'auto',
                    margin: 'auto',
                    width: '70%'
                }
            }}
            onRequestClose={() => setVisible(false)}
        >
            <div>
                {history != '' ? JSON.parse(history).map((item, index) => {
                    let data = new Date(new Date(item.data).getTime() + 86400000)

                    return (
                        <div style={{
                            display: 'flex',
                            borderWidth: 1,
                            borderColor: '#000',
                            borderStyle: 'solid',
                            marginTop: 10,
                            marginBottom: 10,
                            padding: 10,
                            alignItems: 'center',
                            justifyContent: 'space-between',
                            borderRadius: 10
                        }}>
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <span>Pago dia</span>


                                <span>{format(data, "dd/MM/yy")}</span>
                            </div>


                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <span>Valor Pago</span>


                                <span>R$ {parseFloat(item.valor.replace(",", ".")).toLocaleString('pt-BR', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</span>
                            </div>

                        </div>
                    )
                })
                    : <span style={{ display: 'flex', justifyContent: 'center' }}>Nenhum registro encontrado</span>
                }
            </div>
        </ReactModal>
    )
}