import React, { useState } from "react";
import "./index.css"
import db from "../../Scripts/ConfigFirebase";

export default function InitialScreen({ registered, setModal }) {

    return (
        <>
            <div className="container-initial-screen">
                <span>Irmãos Registrados: {registered}</span>

                <button
                    className="btn-register-peoples"
                    onClick={() => setModal(prev => !prev)}
                >Registrar Pessoas</button>

            </div>
                
        </>
    )
}