import React, { useState } from "react";
import { css, keyframes } from '@emotion/react';
import { IoCloseCircleOutline } from "react-icons/io5"
import ModalLoading from "../../Components/Modals/ModalLoading";
import "./index.css"

export default function ModalRegisterPeople({ visible, setForm, form, setVisible, onConfirm }) {

    const [modalLoading, setModalLoading] = useState(false)

    const fadeIn = keyframes`
  0% {opacity: 0;}
  100% {opacity: 1;}
`;

    const fadeOut = keyframes`
  0% {opacity: 1;}
  100% {opacity: 0;}
`;

    return (
        <>
            <ModalLoading visible={modalLoading} title="Aguarde! Salvando dados..."/>
            {visible &&
                <div
                    css={css`
                    animation: ${visible ? fadeIn : fadeOut} 5.0s ease-in-out;
                  `}
                    style={{
                        position: 'absolute',
                        width: '100%',
                        height: '100%',
                        backgroundColor: 'rgba(0,0,0,0.4)',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center'
                    }}

                >
                    <div style={{
                        backgroundColor: '#fff',
                        width: "40%",
                        borderRadius: 10,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        position: 'relative',
                        padding: 20,
                        gap: 10
                    }}>
                        <span className="icon-close-modal"
                            onClick={() => {
                                setVisible(false)
                                setForm("")
                            }}
                        >
                            <IoCloseCircleOutline
                                size={26}
                                color="#000"
                                style={{ position: 'absolute', top: 4, right: 4 }}
                            />
                        </span>

                        <span>Insira o nome</span>
                        <input
                            type="text"
                            placeholder="Nome..."
                            value={form}
                            onChange={e => setForm(e.target.value)}
                            style={{ display: 'flex', width: '80%' }}

                        />

                        <button onClick={onConfirm}>
                            Registrar
                        </button>
                    </div>
                </div>
            }
        </>
    )
}