import React, { use, useEffect, useState } from "react";
import "./index.css"
import { IoTrash, IoPencil, IoAddCircle } from "react-icons/io5"
import { format } from "date-fns"
import ModalLoading from "../../Components/Modals/ModalLoading";
import ModalEdit from "./ModalEdit";

export default function SeeList({ list }) {

    const [registers, setRegisters] = useState([])
    const [newUser, setNewUser] = useState({ date: new Date(), name: '', cpfORrg: '', paid: '' })
    const [modalLoading, setModalLoading] = useState(false)
    const [modalEdit, setModalEdit] = useState(false)
    const [userDataSelected, setUserDataSelected] = useState("")

    useEffect(() => {
        setModalLoading(true)
        getDataFromSheet()
            .then(() => {
                setModalLoading(false)
            })
        const interval = setInterval(getDataFromSheet, 5000)

        return () => clearInterval(interval)
    }, [])
    const orderedList = list.sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()))

    const handleButtonAdd = () => {
        if (newUser.name != '') {
            setModalLoading(true)
            const sheetName = encodeURIComponent("ARRANJO DE ONIBUS");

            // Formatação dos dados do usuário em formato de array para enviar via URL
            const data = JSON.stringify([
                format(newUser.date, "dd/MM/yy"),
                newUser.name.toUpperCase().trim(),
                newUser.cpfORrg.toUpperCase().trim(), '',
                newUser.paid.toUpperCase().trim() == '' ? 0 : newUser.paid.toUpperCase().trim()
            ]);

            // Montagem da URL corretamente
            const url = `https://script.google.com/macros/s/AKfycbwz4hog7Ovzi2CEQFUMTzPxzHuSAqst2N_BvTNN9vxiS9P6MKhL4PpMB2mz6A2xlAws/exec?func=preencherLinhas&sheetName=${sheetName}&data=${encodeURIComponent(data)}`;

            fetch(url, { mode: 'no-cors' })
                .then(async (responseData) => {
                    setRegisters(await getDataFromSheet())
                    setNewUser({ date: new Date(), name: '', cpfORrg: '', paid: '' }); // Reseta os dados
                    setModalLoading(false)
                })
                .catch(error => {
                    setModalLoading(false)
                    alert("Erro na requisição: " + error);
                });
        } else {
            alert("O nome deve ser selecionado")
        }

    }

    const addNewRegister = () => {
        return (
            <tr>
                <td style={{ width: '10%' }}>
                    <div style={{ display: 'flex', flex: 1, justifyContent: 'center', alignItems: 'center' }}>
                        <span>Hoje</span>
                    </div>
                </td>
                <td>
                    <select style={{ width: '100%' }}
                        value={newUser.name}
                        onChange={event => setNewUser(prev => ({ ...prev, name: event.target.value }))}
                    >
                        <option value="">Selecione o nome</option>

                        {orderedList.map((people) => {
                            return <option value={people.name}>{people.name}</option>
                        })}

                    </select>
                </td>

                <td style={{ width: '10%' }}>
                    <input placeholder="Insira o Nº Doc."
                        value={newUser.cpfORrg}
                        onChange={event => setNewUser(prev => ({ ...prev, cpfORrg: event.target.value }))}
                        type="number"
                    />
                </td>

                <td style={{ width: '8%' }}>
                    <input placeholder="Insira o total pago"
                        value={newUser.paid}
                        onChange={event => setNewUser(prev => ({ ...prev, paid: event.target.value }))}
                        type="number"
                    />
                </td>

                <td style={{ width: '5%' }}>
                    <div style={{ display: 'flex', flex: 1, justifyContent: 'center', alignItems: 'center' }}>
                        <span className="button" onClick={() => handleButtonAdd()}>
                            <IoAddCircle
                                size={25}
                            />
                        </span>
                    </div>

                </td>
            </tr>
        )
    }

    const getDataFromSheet = () => {
        return new Promise((resolve, reject) => {
            const url = "https://script.google.com/macros/s/AKfycbwz4hog7Ovzi2CEQFUMTzPxzHuSAqst2N_BvTNN9vxiS9P6MKhL4PpMB2mz6A2xlAws/exec?func=getAllData";

            fetch(url)
                .then((response) => response.json())
                .then((data) => {
                    const list = data.map((item) => ({
                        data: item[0],
                        nome: item[1],
                        cpfOUrg: item[2],
                        pagoDias: item[3],
                        totalPago: parseFloat(item[4]).toLocaleString("pt-BR", {
                            minimumFractionDigits: 2,
                            maximumFractionDigits: 2,
                        }),
                    }));

                    // Resolve a promise com a lista formatada
                    setRegisters(list)
                    resolve(list);
                })
                .catch((err) => {
                    // Rejeita a promise com o erro
                    reject("Erro ao buscar dados: " + err);
                });
        });
    };

    const deleteRegister = (linha) => {
        setModalLoading(true)
        const url = `https://script.google.com/macros/s/AKfycbyk5tUCIDBfPXZDXOBMKnWa8m4Vh5bDcXAAJ-91oBX2V1XH6Y-M_hWTpluVwdE7SKlH/exec?func=deleteRow&linha=${linha}&aba=ARRANJO%20DE%20ONIBUS`

        fetch(url)
            .then(res => res.json())
            .then(() => {
                getDataFromSheet()
                    .then(() => setModalLoading(false))
            })
            .catch(err => {
                setModalLoading(false)
                console.error("Erro:", err)
            });
    }

    return (
        <>
            <ModalLoading visible={modalLoading} title="Aguarde! Buscando dados no servidor..." />

            {modalEdit && <ModalEdit
                visible={modalEdit}
                setVisible={setModalEdit}
                onClose={setModalEdit}
                userData={userDataSelected}
                setUserData={setUserDataSelected}
            />}

            <div style={{ width: "100%", alignItems: 'center', flexDirection: 'column', display: 'flex' }}>


                <table>
                    <thead>
                        <tr>
                            <th style={{ width: '10%' }}>Data Últ. Pag.</th>
                            <th style={{}}>Nome Completo</th>
                            <th style={{ width: '10%' }}>Nº CPF ou RG</th>
                            <th style={{ width: '8%' }}>Total Pago</th>
                            <th style={{ width: '5%' }}>Ações</th>
                        </tr>
                    </thead>
                    <tbody>
                        {registers.map((item, index) => {
                            return (
                                <tr>
                                    <td style={{ width: '10%' }}>
                                        <div style={{
                                            display: 'flex',
                                            flex: 1,
                                            alignItems: 'center',
                                            justifyContent: 'center'
                                        }}>
                                            <span>{format(item.data, "dd/MM/yy")}</span>
                                        </div>
                                    </td>
                                    <td style={{}}>{item.nome}</td>
                                    <td style={{ width: '10%' }}>{item.cpfOUrg}</td>
                                    <td style={{ width: '8%' }}>R$ {item.totalPago}</td>

                                    <td style={{ width: '5%' }}>
                                        <div style={{
                                            display: 'flex',
                                            flex: 1,
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            gap: 8
                                        }}>
                                            <span className="button"
                                                onClick={() => deleteRegister(index + 3)}
                                            >
                                                <IoTrash
                                                    size={24}
                                                    color="#fff"
                                                />
                                            </span>

                                            <span className="button" onClick={() => {
                                                setUserDataSelected({ ...item, index })
                                                setModalEdit(true)
                                            }}>
                                                <IoPencil
                                                    size={24}
                                                    color="#fff"
                                                />
                                            </span>
                                        </div>

                                    </td>
                                </tr>
                            )
                        })}


                        {addNewRegister()}
                    </tbody>

                </table>
            </div>
        </>
    )
}