import logo from './logo.svg';
import './App.css';
import { useEffect, useState } from 'react';
import scripts from './Scripts/main';
import InitialScreen from "./Screens/InitialScreen"
import SeeList from './Screens/SeeList';
import BusMap from './Screens/BusMap'
import db from "./Scripts/ConfigFirebase"
import ModalRegisterPeople from "./Screens/InitialScreen/ModalRegisterPeople";
import { doc, collection, addDoc, setDoc, updateDoc, getDoc, getDocs, onSnapshot } from 'firebase/firestore';

function App() {

  const [menuSelected, setMenuSelected] = useState(1)
  const [peoplesList, setPeoplesList] = useState([])
  const [modalRegisterPeople, setModalRegisterPeople] = useState(false)
  const [inputsRegisterPeoples, setInputsRegisterPeoples] = useState("")

  useEffect(() => {
    scripts()
    getPeoples()
  }, [])

  const getPeoples = async () => {

    const unsubscribe = onSnapshot(collection(db, "Peoples"), (querySnapshot) => {
      const list = []
      querySnapshot.docs.map(item => list.push(item.data()))
      setPeoplesList(list)
    })

    return () => unsubscribe()
  }

  const saveNewPeople = async (name) => {
    try {
      await addDoc(collection(db, "Peoples"), { name: name })
      alert("Usuário adicionado com sucesso!")
      setModalRegisterPeople(false)
      setInputsRegisterPeoples("")
    } catch (error) {
      alert(error.messsage)
    }
  }

  return (
    <div className="main">
      <header>
        <ul className='headerList'>

          <li
            className='headerItemList'
            id='menu-home'
            style={{ backgroundColor: menuSelected === 1 ? "#DAD2FF" : "#B2A5FF" }}
            onClick={() => setMenuSelected(1)}
          ><span>Tela Inicial</span></li>

          <li
            className='headerItemList'
            id='menu-seeList'
            style={{ backgroundColor: menuSelected === 2 ? "#DAD2FF" : "#B2A5FF" }}
            onClick={() => setMenuSelected(2)}
          ><span>Ver Lista</span></li>

          <li
            className='headerItemList'
            id='menu-busMap'
            style={{ backgroundColor: menuSelected === 3 ? "#DAD2FF" : "#B2A5FF" }}
            onClick={() => setMenuSelected(3)}
          ><span>Mapa Ônibus</span></li>

        </ul>
      </header>

      <main className='contentContainer'>
        {menuSelected == 1 &&
          <InitialScreen
            registered={peoplesList.length}
            setModal={setModalRegisterPeople}
          />
        }
        {menuSelected == 2 && <SeeList list={peoplesList ?? []} />}
        {menuSelected == 3 && <BusMap />}

      </main>

      <ModalRegisterPeople
        visible={modalRegisterPeople}
        setVisible={setModalRegisterPeople}
        form={inputsRegisterPeoples}
        setForm={setInputsRegisterPeoples}
        onConfirm={() => saveNewPeople(inputsRegisterPeoples)}
      />
    </div>
  );
}

export default App;
